// ---------------------------------------------------------------------
// ------------------------------  TOOTED ------------------------------
// ---------------------------------------------------------------------

.tooted {
    padding-top: 61px;
    &__desc {
        font-size: 22px;
        line-height: 26px;
        text-align: center;
        margin-top: 18px;
        color: $c-g;
    }
    &__items {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;

        margin-top: 20px;
        li {
            max-width: 31%;
            position: relative;
            margin-top: 30px;
            width: 100%;
            height: auto;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-pack: center;
                -ms-flex-pack: center;
                    justify-content: center;
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
            overflow: hidden;
            a {
                height: 100%;
                width: 100%;
                img {
                    -webkit-transform: scale(1.2);
                            transform: scale(1.2);
                    -o-object-fit: cover;
                       object-fit: cover;
                    -webkit-transition: all 0.5s ease-out;
                    transition: all 0.5s ease-out;
                }
            }
            

            .tooted__item-overlay {
                position: absolute;
                width: 100%;
                height: 100%;
                background: rgba(39,39,39,0.58);
                z-index: 2;
                bottom: 0;
                -webkit-transition: all 0.5s ease-out;
                transition: all 0.5s ease-out;
                left: 0;
            }
            .tooted__item-title {
                position: absolute;
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-pack: center;
                    -ms-flex-pack: center;
                        justify-content: center;
                -webkit-box-align: center;
                    -ms-flex-align: center;
                        align-items: center;
                width: 100%;
                height: 100%;
                bottom: 0;
                z-index: 3;
                font-family: Futura_L;
                font-size: 44px;
                color: #fff;
                line-height: 52px;
                -webkit-transition: all 0.5s ease-out;
                transition: all 0.5s ease-out;
                left: 0;
            }
            -webkit-transition: all 0.5s ease;
            transition: all 0.5s ease;
            &:hover {
                margin-top: 16px;
                -webkit-box-shadow: 0 5px 20px 3px rgba(0,0,0, 0.46);
                        box-shadow: 0 5px 20px 3px rgba(0,0,0, 0.46);
               
                img {
                    -webkit-transform: scale(1);
                            transform: scale(1);
                    
                    min-width: 100%;
                    min-height: 100%;
                    
                }
                .tooted__item-overlay {
                    height: 30%;
                    bottom: 0;
             
                }
                .tooted__item-title {
                    height: 30%;
                    bottom: 0;
                  
                }
            }
        }
        
    }
    

    @media screen and (max-width: 991px){
        &__desc {
            font-size: 20px;
            margin-top: 15px;
        }
        .tooted__items li .tooted__item-title {
            font-size: 36px;
        }
    }
    @media screen and (max-width: 767px){
        .tooted__items li {
            max-width: 47%;
        }
    }
    @media screen and (max-width: 575px){
        padding-top: 30px;
    }
    @media screen and (max-width: 500px){
        &__desc {
            font-size: 18px;
            margin-top: 0px;
        }
        .tooted__items li {
            max-width: 90%;
            margin-left: auto;
            margin-right: auto;
        }
    }
    @media screen and (max-width: 360px){
        &__desc {
            font-size: 16px;
        }
    }
}









// ---------------------------------------------------------------------
// ------------------------------  Brands ------------------------------
// ---------------------------------------------------------------------

.brands {
    
    
    max-width: 100%;
    
    

    margin-top: 73px;

    &-item {
        position: relative;
        
        height: 140px;
        margin-right: 30px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        img {
            position: absolute;
            width: 100%;
            height: auto;
            -webkit-filter: grayscale(1);
            filter: grayscale(100%);
            -webkit-transition: all 0.5s linear 0ms;
            transition: all 0.5s linear 0ms;
            -webkit-transform: scale(1);
                    transform: scale(1);
            
            
    
            &:last-child {
                margin-right: 0;
            }
        }
        &:hover {
            img {
                -webkit-filter: none;
                        filter: none;
                -webkit-transform: scale(1.1);
                        transform: scale(1.1);
            }
            

        }
    }
    
    
}
.read-more-section-block {
    text-align: center;
    margin-top: 82px;
    position: relative;
    width: auto;
    .read-more-section {
        transition: 0.5s ease;
        span {
            position: relative;
            display: inline-block;
            transition: 0.5s ease;
        }
        &:hover {
            color: $c-r;
            span {
                
                transform: scale(1.35);
                        
            }
        }
        
    }
    @media screen and (max-width: 767px){
        margin-top: 40px;
    }
}








