                -ms-flex-pack: center;
                    justify-content: center;
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
            -webkit-transform: rotate(45deg);
                    transform: rotate(45deg);
            -webkit-transition: all 0.4s linear 0ms;
            transition: all 0.4s linear 0ms;

            div {
                -webkit-transform: rotate(-45deg);
                        transform: rotate(-45deg); 
            }
        }
        &:hover {
            .outlet-product__discount {
                background: $c-r;
                color: #fff;
            }
            .outlet-product__cart-vaata {
                color: $c-r;
            }
        }
        a {
            .outlet-product__img {
                img {
                    width: 100%;
                    height: auto;
                }
            }
            .outlet-product__title {
