                color: $c-dg;
            }
        }
        
        &__price {
            margin-top: 25px;
            font-family: Futura_L;
            font-size: 16px;
            line-height: 19px;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-pack: justify;
                -ms-flex-pack: justify;
                    justify-content: space-between;
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
            &-tava {
                color: $c-g;
                // overflow: hidden;
                margin-bottom: -3px;
                span {
                    position: relative;
                    font-size: 20px;
                    font-family: Futura_M;
                    line-height: 24px;
                    
                    &:after {
                        content: '';
                        display: block;
                        width: 130%;
                        height: 1px;
                        background: $c-dg;
                        position: absolute;
                        top: 14px;
                        right: -7px;
                        -webkit-transform: rotate(14deg);
                                transform: rotate(14deg);
                    }
                }
            }
            &-outlet {
                color: $c-r;
                line-height: 19px;
                text-align: end;
                span {
                    font-size: 25px;
                    font-family: Futura_M;
                    line-height: 30px;
                }
