                }
                &-outlet {
                    text-align: end;
                }
            }
        }
    }
    @media screen and (max-width:767px){
        .outlet-product {
            width: 45%;

        }
    }
    @media screen and (max-width:576px){
        padding: 0 15px;
        .outlet-product {
            margin-top: 50px;
            width: 47%;
            &__discount {
                zoom: 90%;
            }
        }
    }
    @media screen and (max-width:460px){

        .outlet-product {
            &__discount {
                zoom: 70%;
            }
            a {
                .outlet-product__title {
                    font-size: 21px;
                    line-height: 24px;
                }
            }
            &__price {
                font-size: 15px;
            }
        }
    }

    @media screen and (max-width:430px){
        .outlet-product {
           width: 90%;
           margin: 50px auto 0 auto;
            &__discount {
                zoom: 90%;
            }
            &__cart {
                &-btn {
                    width: 150px;
                    span {
                        display: inline;
                    }
                }
            }
        }
    }

    @media screen and (max-width:360px){
        .outlet-product {

    
            &__cart {
 
                &-btn {
                    width: 100px;
                    span {
                        display:none;
                    }
                }
            }
