
// ---------------------------------------------------------------------
// ------------------------------  TEAM ------------------------------
// ---------------------------------------------------------------------

.inspiraDesign-team {
    margin-bottom: 190px;
    margin-top: 0;

    @media screen and (max-width: 780px){
        margin-bottom: 230px;
    }
    @media screen and (max-width: 767px){
        margin-bottom: 300px;
    }
    @media screen and (max-width: 454px){
        margin-bottom: 330px;
    }
}


.vota-team {
    .h2-big.vota-team__h2 {
        margin-bottom: 15px;
        @media screen and (max-width: 499px){
            h2 {
                font-size: 60px;
            }
            div {
                font-size: 60px;
            }
            
           
        }
        @media screen and (max-width: 360px){
            margin-top: 30px;
            h2 {
                font-size: 52px;

            }
            div {
                font-size: 52px;
            }
            
            
        }
    }
    &__desc {
        text-align: left;
        max-width: 772px;
        margin: 0 auto;
        font-size: 16px;
        line-height: 21px;
    }
    @media screen and (max-width: 889px) {
        padding-bottom: 21px;
    }
    @media screen and (max-width: 774px) {
        padding-bottom: 0px;
    }
    @media screen and (max-width: 767px) {
        padding-bottom: 41px;
    }
    @media screen and (max-width: 454px) {
        padding-bottom: 20px;
    }
}


