
// ---------------------------------------------------------------------
// ------------------------------  NEWS ARTICLE ------------------------------
// ---------------------------------------------------------------------

.pageTitle-newsArticle {
    padding: 60px 0;
    background: url(../img/news-article/news-article-bg.jpg) no-repeat center bottom/ cover;
    position: relative;
    z-index: 0;
    
    &:after {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0, 0.65);
        z-index: -1;
    }
    
    &_h1 {
        font-family: Futura_L;
        font-size: 48px;
        line-height: 57px;
        color: #fff;
        text-shadow: 0px 3px 6px rgba(0,0,0, 0.76);
    }
    @media screen and (max-width: 991px){
        &_h1 {
            font-size: 41px;
            line-height: 40px;
        }
    }
    @media screen and (max-width: 768px){
        padding: 35px 0;
        &_h1 {

            font-size: 31px;
            line-height: 36px;
        }
    }
    @media screen and (max-width: 335px){
        
        &_h1 {
            font-size: 28px;
        }
    }
}




// ---------------------------------------------------------------------
// ------------------------------  ARTICLE ------------------------------
// ---------------------------------------------------------------------

.newsArticle {
    padding-bottom: 80px;
    &__block {
        &_img {
            max-width: 100%;
            height: auto;
        }
        &_text {
            color: $c-g;
            margin-top: 38px;
            line-height: 21px;
            p {
                margin-bottom: 21px;
            }
            p:last-child {
                margin-bottom: 0;
            }
        }
        &_btn {
            margin-top: 53px;
            justify-content: flex-start;
            .uudised-item__text-btn_arrow {
                margin-right: 15px;
                font-size: 24px;
            }
            @media screen and (max-width: 767px) {
                .uudised-item__text-btn_arrow {
                    width: 50px;
                    height: 60px;
                }
            }
        }
    }
    
}


