.section-navHP {
  
    @media screen and (min-width: 768px){
        .drawer-list {
            margin-top: 35px;
            width: 100%;
            display: flex;
            z-index: 99;
            .top-menu-hp {
                width: 100%;
                display: flex;
                margin-bottom: 0;
                justify-content: space-between;
                z-index: 99;
                
                li {
                    position: relative;
                    z-index: 99;
                    .sub-menu {
                      padding: 11px 4px 20px 30px;
                      display: none;
                      width: 228px;
                      position: absolute;
                      left: -37px;
                      top: 32px;
                      height: auto;
                      border-left: 4px solid #77787B;
                      box-shadow: 0px 0px 10px 1px rgba(32,32,32,0.1);
                      li {
                        a {
                          font-family: Raleway_M;;
                          font-size: 18px;
                          line-height: 40px;
                          
                        }
                      }
                    }
                    &.open{
                      .sub-menu {
                        display: block;
                      }
                      a {
                          align-items: flex-end;
                          &:hover {
                            color: #BF4949;
                            transition: 0.3s;
                        }
                      }
                    }
                    a {
                        font-size: 20px;
                        font-family: Futura_M;
                        color: $c-dg;
                        line-height: 24px;
                        display: flex;
                        align-items: center;
                        &:hover {
                            color: #BF4949;
                            transition: 0.3s;
                        }
                        svg {
                          font-size: 10px;
                          margin-left: 10px;
                        }
                    }
                    
                }
                
            }
            &__tel {
                min-width: 140px;
                margin-left: 50px;
                &-img img {
                    margin-right: 15px;
                }
                a {
                    font-size: 16px;
                    line-height: 27px;
                    font-family: Future_M;
                    color: $c-dg;
                    span {
                        font-family: Future_L;
                        font-size: 14px;
                        
                    }
                }
            }
        }
    }
    .sub-menu {
        display: none;
        background: #fff;
        z-index: 9;
    }


    @media screen and (max-width: 767px){
        
        .drawer-list {
          z-index: 99;
            position: fixed;
            right: 0;
            top: 0;
            height: 100vh;
            width: 50vw;
            transform: translate(100vw, 0);
            /* ie workaround */
            -ms-transform: translatex(-100vw);
            box-sizing: border-box;
            pointer-events: none;
            padding-top: 70px;
            transition: width 475ms ease-out, transform 450ms ease, border-radius .8s .1s ease;
            border-bottom-left-radius: 100vw;
            
            background-color: rgba(22, 22, 22, 0.95);
            .top-menu-hp {
              max-height: calc(100vh - 30px);
              overflow-y: auto !important;
              
            }
            ul {
              li a {
                padding-right: 0;
              }
            }
            &__tel {
                min-width: 140px;
                text-align: center;
                &-img img {
                    margin-right: 15px;
                }
                a {
                    font-size: 16px;
                    line-height: 27px;
                    font-family: Future_M;
                    color: #fff;
                    span {
                        font-family: Future_L;
                        font-size: 14px;
                        
                    }
                }
            }
          }
          
          .drawer-list ul {
            height: auto;
            width: 100%;
            margin: 0;
            padding: 0;
            overflow: auto;
            overflow-x: hidden;
            pointer-events: auto;
            &::-webkit-scrollbar {width: 0px;
                  height: 0;
              }
            -ms-overflow-style: none;  
          }
          .drawer-list li {
            list-style: none;
            text-transform: uppercase;
            pointer-events: auto;
            white-space: nowrap;
            box-sizing: border-box;
            transform: translatex(100vw);
            /* ie workaround */
            -ms-transform: translatex(-100vw);
          }
          
          .drawer-list li a {
            text-decoration: none;
            color: #FEFEFE;
            text-align: center;
            display: block;
            padding: 1rem;
            font-size: calc(24px - .5vw);
          }
          @media (min-width: 768px) {
            .drawer-list li a {
              text-align: right;
              padding: .5rem;
            }
          }

          
          input.hamburger {
            display: none;
          }
          input.hamburger:checked ~ .drawer-list {
            transform: translatex(0);
            border-bottom-left-radius: 0;
          }
          input.hamburger:checked ~ .drawer-list li {
            transform: translatex(0);
          }
          input.hamburger:checked ~ .drawer-list li:nth-child(1) {
            transition: transform 1s 0.08s cubic-bezier(0.29, 1.4, 0.44, 0.96);
          }
          input.hamburger:checked ~ .drawer-list li:nth-child(2) {
            transition: transform 1s 0.16s cubic-bezier(0.29, 1.4, 0.44, 0.96);
          }
