          input.hamburger:checked ~ .drawer-list li:nth-child(49) {
            transition: transform 1s 3.92s cubic-bezier(0.29, 1.4, 0.44, 0.96);
          }
          input.hamburger:checked ~ .drawer-list li:nth-child(50) {
            transition: transform 1s 4s cubic-bezier(0.29, 1.4, 0.44, 0.96);
          }
          
          input.hamburger:checked ~ label.hamburger > i {
            background-color: transparent;
            transform: rotate(90deg);
          }
          input.hamburger:checked ~ label.hamburger > i:before {
            transform: translate(-50%, -50%) rotate(315deg);
          }
          input.hamburger:checked ~ label.hamburger > i:after {
            transform: translate(-50%, -50%) rotate(-315deg);
          }
          input.hamburger:checked ~ label.hamburger close {
            color: #FEFEFE;
            width: 100%;
          }
          input.hamburger:checked ~ label.hamburger open {
            color: rgba(0, 0, 0, 0);
            width: 0;
          }
          
          label.hamburger {
            z-index: 9999;
            position: relative;
            display: block;
            height: 47px;
            width: 47px;
          }
          label.hamburger:hover {
            cursor: pointer;
          }
          label.hamburger text close,
          label.hamburger text open {
            text-transform: uppercase;
            font-size: .8em;
            align-text: center;
            position: absolute;
            transform: translateY(50px);
            text-align: center;
            overflow: hidden;
            transition: width .25s .35s, color .45s .35s;
          }
          label.hamburger text close {
            color: rgba(0, 0, 0, 0);
            right: 0;
            width: 0;
          }
          label.hamburger text open {
            color: #FEFEFE;
            width: 100%;
          }
          label.hamburger > i {
            position: absolute;
            width: 100%;
            height: 2px;
            top: 50%;
            background-color: #FEFEFE;
            pointer-events: auto;
            transition-duration: .35s;
            transition-delay: .35s;
          }
          label.hamburger > i:before, label.hamburger > i:after {
            position: absolute;
            display: block;
            width: 100%;
            height: 2px;
            left: 50%;
            background-color: #FEFEFE;
            content: "";
            transition: transform 0.35s;
            transform-origin: 50% 50%;
          }
          label.hamburger > i:before {
            transform: translate(-50%, -12px);
          }
          label.hamburger > i:after {
            transform: translate(-50%, 12px);
          }
          
          label.hamburger {
            position: fixed;
            top: 12px;
            right: 15px;
          }

