        background: rgba(31, 30, 30, 0.7);
        border-radius: 9px;
        // box-shadow: 0px 0px 5px 2px rgba(32,32,32,.5);
        text-align: left;
        filter: contrast(1.3) drop-shadow(2px 7px 6px rgba(0,0,0,.3));
        &:hover {
          
        }
        text open {
          color: #fff;
          
        }
        i {
          background: #fff;
          width: 70%;
          left: 15%;
          
          &:before {
            background: #fff;
          }
          &:after {
            background: #fff;
          }
        }
      }


      .drawer-list {

          .top-menu-hp {
            overflow: visible;
            color: #fff;
            height: 73vh;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            margin-top: 20px;
            padding-bottom: 20px;
            
              li {
                margin-bottom: 15px;
                  position: relative;
                  
                  a {
                      font-size: 22px;
                      font-family: Futura_M;
                      line-height: 40px;
                      padding: 0;
                      &.withArrow {
                        margin-right: -28.5px;
                      }
                      
                      svg {
                        font-size: 13px;
                        margin-left: 20px;
                      }
                      @media screen and (max-width: 767px) {
                        &.withArrow {
                          margin-right: 0px;
                        }
                        svg {
                          position: absolute;
                          top: 7px;
                        }
                      }
                  }
                  .sub-menu {
                    
                    display: none;
                    
                    position: relative;
                    padding-left: calc(50vw - 28px);
                    padding-right: 10px;
                    height: auto;
                    
                    background: none;
                    width: 100%;
                    
                    margin-top: 15px;
                    margin-bottom: 5px;
                    
                    li {
                      margin-bottom: 0;
                      width: 100%;
                      &:last-child {
