
// colors
$c-lg: #F2F1F1;
$c-g: #6D6D6D;
$c-p: #F9E8E8;
$c-r: #BF4949;
$c-b: #143E5E;
$c-dg: #222222;

@font-face {
    font-family: Futura_H; /*   */
    src: url(../fonts/Futura_Heavy_BT.ttf); /*      */
}
@font-face {
    font-family: Futura_L; /*   */
    src: url(../fonts/Futura_Light_BT.ttf); /*      */
}
@font-face {
    font-family: Futura_M; /*   */
    src: url(../fonts/Futura_Md_BT_Medium.ttf); /*      */
}
@font-face {
    font-family: Futura_B; /*   */
    src: url(../fonts/Futura_Md_BT_Bold.ttf); /*      */
}
@font-face {
    font-family: Raleway_R; /*   */
    src: url(../fonts/Raleway-Regular.ttf); /*      */
}
@font-face {
    font-family: Raleway_M; /*   */
    src: url(../fonts/Raleway-Medium.ttf); /*      */
}
@font-face {
    font-family: Raleway_B; /*   */
    src: url(../fonts/Raleway-Bold.ttf); /*      */
}



.raleway-b {
    font-family: Raleway_B;
}
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    // border: 1px solid red;
    
    // &::-webkit-scrollbar {width: 0px;
    //     height: 0;
    // }
    
}

button {
    border: none;
    background: none;
    outline: none;
    &:focus {
        outline: none;
    }
}
body {
    font-size: 16px;
    font-family: Raleway_R;
    color: $c-dg;
    max-width: 100vw;
    overflow-x: hidden;
    line-height: 21px;
    
    
}

a {
    text-decoration: none;
    text-transform: none;
    outline: none;
    color: $c-dg;
    &:focus {
        text-decoration: none;
        text-transform: none;
        outline: none;
    }
    &:active {
        text-decoration: none;
        text-transform: none;
        outline: none;
    }
    &:hover {
        text-decoration: none;
        text-transform: none;
        outline: none;
        color: #BF4949;
    }
}
.h1-hp {
    font-size: 41px;
    line-height: 54px;
    font-family: Futura_L;
    color: #fff;
    span {
        font-family: Futura_M;
    }
}
.h1-p {
    font-size: 122px;
    line-height: 163px;
    font-family: Futura_L;
    text-transform: uppercase;
    color: #fff;
    text-shadow: 0px 3px 6px rgba(0, 0, 0, 0.76);
    text-align: center;
}
.h2 {
        display: flex;
        justify-content: center;
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        position: relative;
        height: 127px;
        font-size: 95px;
        line-height: 127px;
        
    h2 {
        font-size: 95px;
        line-height: 127px;
        font-family: Futura_L;
        text-transform: uppercase;
        text-align: center;
        height: 127px;
        color: #fff;
        text-shadow: 0px 3px 6px rgba(0, 0, 0, 0.25);
        position: absolute;
        width: 100%;
        text-align: center;
        
    }
    div {
        position: absolute;
        font-size: 95px;
        line-height: 127px;
        white-space: nowrap;
        height: 60%;
        overflow: hidden;
        font-family: Futura_L;
        text-transform: uppercase;
        width: 100%;
        text-align: center;
    }
}

h3 {
    font-family: Futura_L;
    font-size: 28px;
    line-height: 34px;
    margin: 0;
    a {
        font-family: Futura_L;
        font-size: 28px;
        line-height: 34px;
        margin: 0;
        
    }
}
h4 {
    font-family: Futura_M;
    font-size: 18px;
    line-height: 24px;
}
.read-more-section {
    font-family: Futura_L;
    font-size: 22px;
    line-height: 30px;
    color: $c-dg;
    &:hover {
        color: $c-dg;
    }
    span {
        color: $c-r;
        font-size: 24px;
        position: relative;
        top: 2px;
        i {
            
        }
    }
}
.read-more-article {
    font-family: Futura_L;
    font-size: 18px;
    line-height: 21px;
    color: $c-dg;
    &:hover {
        color: $c-dg; 
    }
}
ul {
    list-style-type: none;
    margin-bottom: 0;
}
section {
    padding-top: 91px;
    padding-bottom: 91px;
}
.pageTitle {
    padding: 70px 0;
}
.team-contacts {
    font-size: 16px;
    font-family: Raleway_B;
    line-height: 21px;
    padding-left: 40px;
    margin-top: 35px;
    li {
        position: relative;
        img {
            position: absolute;
            top: 0;
            left: -40px;
        }
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    
    &__phone {
        span {
            font-family: Raleway_R;
            font-size: 14px;
            
        }
    }
    &__clock {
        font-family: "Arial";
            font-size: 16;
            font-weight: normal;
            span {
                font-size: 14px;
            }
    }
}

@import "header-homepage";
@import "header-pages";

@import "menu-top-hp";
@import "menu-top-p";

@import "homepage";
@import "productCategory";
