.footer {
    background: #333333;
    color: #D9D9D9;
    font-family: "Arial";
        font-size: 18px;
        line-height: 36px;
    padding: 41px 0 46px;
    
    a {
        color: #D9D9D9;
        font-family: "Arial";
        font-size: 18px;
        line-height: 36px;
        &:hover {
            color: #FFB4B4;
            transition: 0.3s;
        }
    }
    &__title {
        a {
            text-transform: uppercase;
            font-family: Futura_L;
            font-size: 28px;
            line-height: 34px;
            margin-bottom: 22px;  
        }
        text-transform: uppercase;
        font-family: Futura_L;
        font-size: 28px;
        line-height: 34px;
        margin-bottom: 22px;
    }

    &__tooted-content {
        display: flex;
        ul{
            width: 43%;
        }
    }
    &__contact {
        height: 100%;
        display: flex;
        flex-direction: column;

        &-decs {
            margin-bottom: 5px;
        }
        ul {
            flex-grow: 1;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            li {
                img {
                    margin-right: 15px;
                    filter: brightness(0) invert(0.9);
                    transition: 0.3s all ease;
                    
                }
                a {
                    line-height: 40px;
                    span {
                        font-size: 14px;
                    }
                }
                &:hover {
                    img {
                        transition: 0.3s all ease;
                        filter:  invert(0.1) brightness(2.0);
                    }
                }
            }
        }
    }

    &__meid {
        height: 100%;
        display: flex;
        flex-direction: column;
        ul {
            flex-grow: 1;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            li {
                img {
                    margin-right: 15px;
                }
                a {
                    img {
                        filter: grayscale(1);
                    }
                    &:hover {
                        img {
                            filter: none;
                        }
                        
                    }
                }
            }
        }
    }

    @media screen and (max-width: 991px){
        &__meid {
            margin-top: 50px;
            align-items: center;
          
            ul {
                width: 100%;
                flex-direction: row;
            }
        }
    }
    @media screen and (max-width: 767px){
        &__meid {
            
          
            ul {
                

            }
        }

        &__contact {
            margin-top: 50px;
            align-items: center;
            &-decs {
                margin-bottom: 5px;
            }
            ul {
                flex-grow: 0;
            }
        }

        &__tooted {
            display: flex;
            flex-direction: column;
            align-items: center;
            &-content {
                width: 100%;
                justify-content: space-around;
                ul {
                    width: auto;
                }
            }
            
        }
    }
    @media screen and (max-width: 575px){
        
        &__meid {
            margin-top: 0;
            .footer__title {
                margin-top: 50px;
            }
            align-items: center;
            ul {
                flex-direction: column;
                justify-content: start;
                width: auto;
                height: auto;
                li {
                    margin-top: 30px;
                    &:first-child {
                        margin-top: 0;
                    }
                }
            }
        }

    }
    @media screen and (max-width: 340px){
        
        &__contact {
            
            ul {
                .tel {
                    a {
                        font-size: 16px;
                    }
                }
            }
        }
    }
}






