.slick-next, .slick-prev {
    overflow: visible !important;
}
.slick-slider {
    .slick-slide {
        outline: none;
    }
}

.tehtudTood-block__sliders, .choosenBrand__sliders {
    .slick-slide {
        outline: none;
    }
    &:before {
        content: "";
        display: block;
        width: 15px;
        height: 100%;
        left: 0;
        top: 0;
        background: $c-lg;
        position: absolute;
        z-index: 999;
    }
    &:after {
        content: "";
        display: block;
        width: 15px;
        height: 100%;
        right: 0;
        top: 0;
        background: $c-lg;
        position: absolute;
        z-index: 999;
    }
}


.chooseCategory-popup .section-form__form-popup {
    &::-webkit-scrollbar {width: 0px;
        height: 0;
    }
  -ms-overflow-style: none; 
}


.shoppingCartBtn {
    width: 55px;
    height: 55px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    bottom: 20px;
    right: 34px;
    box-shadow: 0px 1px 8px 1px rgba(0,0,0, 0.26);
    background: none;
    outline: none;
    border: none;
    z-index: 99;
    background: #fff;
    &:focus {
        outline: none;
    }
    img {
        filter: grayscale(1);
        transform: scale(1.3);
    }
    span {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: -8px;
         right: -8px;
        background: #FFB4B4;
        border-radius: 50%;
        width: 25px;
        height: 25px;
    }
    
}
.shoppingCart__block {
    width: 100vw;
    height: 100vh;
    position: fixed;
    background: rgba(0,0,0, 0.75);
    top: 0;
    left: 0;
    z-index: 999999;
    display: none;
    opacity: 0;
    -webkit-transition: all 0.5s ease;  
        -moz-transition: all 0.5s ease;  
        -o-transition: all 0.5s ease;  
        -ms-transition: all 0.5s ease;  
        transition: all 0.5s ease;

    &_content {
        width: 305px;
        height: 100%;
        background: #fff;
        position: fixed;
        top: 0;
        right: 0;
        padding: 30px 21px 0 23px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        transition: 1s all ease;
        -webkit-transition: all 1s ease;  
        -moz-transition: all 1s ease;  
        -o-transition: all 1s ease;  
        -ms-transition: all 1s ease;  
        transition: all 1s ease;

        
        &-close {
            position: fixed;
            top: 0px;
            right: -3px;
            z-index: 999999;
            cursor: pointer;
            width: 50px;
            height: 50px;
            z-index: 99999;
            transform: scale(0.6);
            &:before {
                content: "";
                display: block;
                width: 50px;
                height: 1.5px;
                background: $c-dg;
                -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg) translate(-20px,20px);
                transition: 0.3s all ease;
                
            }
            &:after {
                content: "";
                display: block;
                width: 50px;
                height: 1.5px;
                background: $c-dg;
                -webkit-transform: rotate(45deg) translate(-1px,-1px);
                transform: rotate(45deg) translate(19px,19px);
                transition: 0.3s all ease;
                
            }
            &:hover {
                &:after, &:before {
                    background: $c-r;
                }
            }
        }
        &-products {
            max-height: calc(100vh - 255px);
            overflow: auto;
            &::-webkit-scrollbar {
                
                -ms-overflow-style: none; /* IE 11 */
                scrollbar-width: none; /* Firefox 64 */
                display: none;
            }
            
            -ms-overflow-style: none; 
        }
        h4 {
            line-height: 21px;
            position: relative;
            transition: 0.3s all ease;
            span {
                display: block;
                position: absolute;
                right: 15px;
                top: calc(50% - 10.5px);
                transform: scale(1.5) translateX(-40px);
                transition: 0.5s all ease;
                opacity: 0;
                color: $c-r;
            }
            &:hover {
                
                
            }
        }
        
        &-title {
            margin-bottom: 13px;
            img {
                position: relative;
                top: -5px;
                transform: scale(1.2);
                margin-right: 8px;
            }
        }
        &-product {
            display: block;
            width: 100%;
            margin-bottom: 31px;
            border-top: 2px solid #E7E7E7;
            padding-top: 22px;
            &:hover {
                h4 {
                    color: $c-dg;
                    &:hover {
                        color: $c-r;
                        span {
                            opacity: 1;
                            transform: scale(1.5) translateX(0);
                        }
                    }
                }
            }
            &-middle {
                display: flex;
                width: 100%;
                margin-top: 15px;
                
                overflow: hidden;
                
                
                &.border-red {
                    
                    .shoppingCart__block_content-product-img {
                        border: 2px solid $c-r;
                    }
                   
                }
            }
            &-img {
                flex: 1;
                
                height: 127px;
                transition: 0.3s all ease;
                
            }
            
            &-delet {
               
                background: $c-lg;
                display: flex;
                justify-content: center;
                align-items: center;
                flex: 0 69px;
               
                transition: 0.3s all ease;
                img {
                    width: auto;
                    height: auto;
                    transition: 0.3s all ease;
                }
                &:hover {
                    background: $c-r;
                    img {
                        filter: brightness(2);
                    }
                }
            }
            &-bottom {
                display: flex;
                justify-content: space-between;
                align-items: center;
                color: $c-g;
                margin-top: 15.5px;
                
                    &_cost {
                        line-height: 21px;
                        span {
                            color: $c-r;
                            font-family: Raleway_M;
                            position: relative;
                            left: 5px;
                        }
                    }
                    &_quantity {
                        position: relative;
                        right: 8px;
                        line-height: 21px;
                        span {
                            font-size: 14px;
                            position: relative;
                            left: 3px;
                            top: -2px;
                        }
                    }
            }
        }

        &-top {
            height: 100%;
        }
        &-bottom {
            border-top: 2px solid #E7E7E7;
            padding-top: 33.5px;
            padding-bottom: 40px;
            &-all {
                display: flex;
                justify-content: space-between;
                font-family: Raleway_B;
                line-height: 21px;
                span {
                    color: $c-r;
                }

            }
            .shoppingCart__btn {
                width: 100%;
                margin-top: 32px;
                border: 1px solid rgba(191, 73, 73, 0.31);
                transition: 0.3s all ease;
                span {
                    color: rgba(191, 73, 73, 0.31);
                    transition: 0.3s all ease;
                }
                &:hover {
                    border: 1px solid rgba(191, 73, 73, 1);
                    span {
                        color: rgba(191, 73, 73, 1);
                    }
                }
            }
        }
    }
    &.open {
        display: block;

    }
    &.opacityBlock{
        opacity: 1;
    }
    &_content {
        transform: translateX(300px);
        &.translateX {
            transform: none;
        }
    }
}

@media screen and (max-width: 495px){
    .chooseCategory-popup .section-form__form-popup .section-form__form_content-area-btn {
        position: relative;
        margin: 50px auto 0 auto;
    }
}