.pageTitle.pageTitle-projects {
    background: url(../img/projects/projects-bg.jpg) no-repeat center center/ cover;
    @media screen and (max-width: 767px){
        .h1-p {
            font-size: 74px;
        }
    }
    @media screen and (max-width: 540px){
        .h1-p {
            font-size: 60px;
            line-height: 120px;
        }
    }
    @media screen and (max-width: 439px){
        .h1-p {
