}

.projects {
    color: #fff;
    background: $c-dg;
    margin-top: -1px;
    padding-top: 45px;
    padding-bottom: 100px;
    
    
    &__item {
        margin-top: 45px;
        .projects__sliders {
            display: none;
        }
        &-h4 {
            font-family: Futura_L;
            font-size: 28px;
            line-height: 34px;
            margin-bottom: 13px;;
        }
        &_block {
            width: 100%;
            height: auto;
            position: relative;
            overflow: hidden;
            display: block;
            &-img{
                width: 100%;
                height: auto;
            }
            .projects__item_block-zoom {
                position: absolute;
                width: 123px;
                height: 134px;
                background: rgba(0,0,0,0.75);
                top: calc(50% - 61.5px);
                left: calc(50% - 61.5px);
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 70px;
                font-family: Futura_L;
                border-radius: 50%;
                opacity: 0;
                zoom: 95%;
                transition: 0.5s all ease;
            }
            &-down {
                position: absolute;
                bottom: 0;
                padding: 15px;
                padding-bottom: 10px;
                background: rgba(0,0,0,0.75);
                width: 100%;
                overflow: hidden;
                opacity: 0;
                transition: 0.5s all ease;
                transform: translateY(67px);
                &-title {
                    font-family: Futura_M;
                    font-size: 16px;
                    line-height: 21px;
                    color: #fff;
                }
                &-list {
                    display: flex;
                    
                    li {
                        margin-left: 15px;
                        font-size: 14px;
                        color: rgba(255,255,255,0.7);
                        white-space: nowrap;
                        font-family: Raleway_R;
                        border-bottom: 1px solid rgba(255,255,255,0.7);
                        span {
                            font-family: Raleway_M;
                            color: #fff;
                        }

                        &:first-child {
                            margin-left: 0;
                        }
                    }
                    li a {
                        margin-left: 15px;
                        font-size: 14px;
                        color: rgba(255,255,255,0.7);
                        white-space: nowrap;
                        font-family: Raleway_R;
                        
                        span {
                            font-family: Raleway_M;
                            color: #fff;
                        }

                        &:first-child {
                            margin-left: 0;
                        }
                    }
                }
            }
            &:hover {
                .projects__item_block-zoom {
                    opacity: 1; 
                }
                .projects__item_block-down {
                    opacity: 1;
                    transform: none;
                }
            }
        }
    }

    .projects__item_content {
        
        
    }


    
    @media screen and (max-width: 991px){
        &__item {
            &-h4 {
                font-size: 25px;
            }
        }
    }
    @media screen and (max-width: 767px){
        padding-top: 25px;
    }
}
.projects__sliders-open {
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0, 0.70);
    top: 0;
    left: 0;
    z-index: 9999;
    display:none;


    

    .projects__sliders-close {
        position: fixed;
        top: 8vh;
        right: 5vw;
        z-index: 999999;
        cursor: pointer;
        width: 50px;
        height: 50px;
        &:after {
            content: "";
            display: block;
            width: 50px;
            height: 1.5px;
            background: #fff;
            transform: rotate(45deg) translate(-1px, -1px);
        }
        &:before {
            content: "";
            display: block;
            width: 50px;
            height: 1.5px;
            background: #fff;
            transform: rotate(-45deg);
            
        }
    }
    .projects__sliders {
        position: fixed;
        max-width: 90vw;
        max-height: 96vh;
        // width: auto;
        top: 4vh;
        left: 6vw;
        z-index: 99999;
        display: block;
        .slick-slide {
            width: 90vw;
            height: auto;
            max-height: 93vh;
            object-fit: cover;
            object-position: center center;
            outline: none;
            &:focus {
                outline: none;
            }
        }
        .slick-prev {
            left: 40px;
            z-index: 1;
            top: 42%;
            opacity: 1;
            transform: translateX(100px);
            transition: 0.5s all ease;
            &:before {
                content: url(../img/homepage/meie/arrow-prev.svg);
                filter: brightness(0) invert(1);
                z-index: 2;
            }
            &:after {
                content: "";
                display: block;
                position: absolute;
                width: 0px;
                height: 0px;
                top: 36px;
                left: 30px;
                background: rgba(0,0,0,0.16);
                box-shadow: 0px 0px 100px 50px rgba(0,0,0, 1); 
                z-index: -1;
            }
        }
        .slick-next {
            right: 64px;
            top: 42%;
            z-index: 1;
            transform: translateX(-100px);
            transition: 0.5s all ease;
            &:after {
                content: "";
                display: block;
                position: absolute;
                width: 0px;
                height: 0px;
                top: 36px;
                right: 20px;
                background: rgba(0,0,0,0.16);
                box-shadow: 0px 0px 100px 50px rgba(0,0,0, 1); 
                z-index: -1;
            }
            &:before {
                z-index: 999;
                filter: brightness(0) invert(1);
                opacity: 2;
                content: url(../img/homepage/meie/arrow-next.svg);
            }
        }
        @media screen and (max-width: 1200px) {
            .slick-prev, .slick-next {
                zoom:90%;
            } 
        }
        @media screen and (max-width: 767px) {
            .slick-prev, .slick-next {
                zoom:75%;
                top: 35%;
            } 
        }
        .slick-prev, .slick-next {
            opacity: 0;
        }
        .slick-prev:before, .slick-next:before {
            opacity: 1;
        }
        &:hover .slick-prev {
            opacity: 1;
            transition: 1s;
            transform: none;
        }
        &:hover .slick-next {
            opacity: 1;
            transition: 1s;
            transform: none;
        }
    }

    @media screen and (orientation: landscape) {

    }
    @media screen and (orientation: portrait) {
        .projects__sliders {
            max-width: 95vw;
            left: 2.5vw;
            top: 10vh;
            .slick-slide {
                width: auto;
                height: 70vh;
                object-fit: cover;
            }
        }
        .projects__sliders-close {
            top: 14vh;
            right: 6vw;
            
            
        }
    }
}
