.pageTitle {
    background: url(../img/product_catecory/productcategory-bg.jpg) no-repeat center center/ cover;
}
.chooseCategory {
    padding: 0;
    z-index: 999;
    
}
.chooseCategory-popup {
    background: rgba(0,0,0, 0.7);
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    z-index: 9999;
    
    justify-content: center;
    align-items: center;
    display: none;
    
    &__block {
        width: auto;
        height: auto;
        width: 75%;
        max-height: 90%;
        display: flex;
        position: relative;
        display: none;
        
        .slick-prev {
            left: -60px;
            z-index: 99;
            top: 42%;
            opacity: 0;
            transform: translateX(200px);
            transition: 0.5s all ease;
            position: absolute;
            z-index: 1;
            &:after {
                content: "";
                display: block;
                position: absolute;
                width: 0px;
                height: 0px;
                top: 36px;
                left: 30px;
                background: rgba(0,0,0,0.16);
                box-shadow: 0px 0px 100px 50px rgba(0,0,0, 1); 
                z-index: -1;
            }
            &:before {
                content: url(../img/homepage/meie/arrow-prev.svg);
                filter: contrast(250%);
                z-index: 2;
                z-index: 9999999;
            }
        }
        .slick-next {
            right: -40px;
            top: 42%;
            z-index: 1;
            transform: translateX(-200px);
            transition: 0.5s all ease;
            opacity: 0;
            &:after {
                content: "";
                display: block;
                position: absolute;
                width: 0px;
                height: 0px;
                top: 36px;
                right: 20px;
                background: rgba(0,0,0,0.16);
                box-shadow: 0px 0px 100px 50px rgba(0,0,0, 1); 
                z-index: -1;
            }
            &:before {
                z-index: 999;
                filter: brightness(0) invert(1) contrast(250%);
                opacity: 1;
                content: url(../img/homepage/meie/arrow-next.svg);
            }
        }
        &:hover {
            .slick-prev {
                opacity: 1;
                transform: translateX(100px);
            }
            .slick-next {
                opacity: 1;
                transform: translateX(-100px);
            }
        }
        &_close {
            position: absolute;
            top: -7px;
            right: -60px;
            color: #fff;
            font-size: 30px;
            padding: 3px;
            align-items: 30px;
            cursor: pointer;
            width: 50px;
            height: 50px;
            &:before {
                content: "";
                display: block;
                width: 50px;
                height: 1.5px;
                background: #fff;
                
                transform: rotate(-45deg) translate(-20px,15px);
            }
            &:after {
                content: "";
                display: block;
                width: 50px;
                height: 1.5px;
                background: #fff;
                
                transform: rotate(45deg) translate(14px,19px);
            }
        }
        img {
            width: 100%;
            max-height: 100%;
            object-fit: cover;
            
        }
    }
    .height {
        max-height: 95vh;
        
        overflow-y: auto;
    }
    .container {
        display: none;
    }
    .section-form__form-popup {
        
        transform: none;
        margin: 0 auto;
        padding: 35px 70px 75px;
        width: 100%;
        max-width: 970px;
        position: relative;
        display: none;
        overflow: visible;
        margin-top: 50px;
        .form-close {
            position: absolute;
            top: -37px;
            right: -67px;
            font-size: 60px;
            color: #fff;
            font-family: Futura_L;
            width: 50px;
            height: 50px;
            cursor: pointer;
            &:before {
                content: "";
                display: block;
                width: 50px;
                height: 1.5px;
                background: #fff;
                
                transform: rotate(-45deg) translate(-20px,15px);
            }
            &:after {
                content: "";
                display: block;
                width: 50px;
                height: 1.5px;
                background: #fff;
                
                transform: rotate(45deg) translate(14px,19px);
            }
            
        }
        .section-form__form_content-input div {
            background: none;
            z-index: 2;
            position: relative;
        }
        .section-form__form_content-input div:before {
            z-index: -1;
            
        }
        &_desc {
            font-size: 22px;
            line-height: 26px;
            color: $c-g;
            text-align: center;
            margin-top: 10px;
        }
        .section-form__form-block {
            width: 100%;
            display: flex;
            justify-content: space-between;
            margin-top: 20px;
        }
        .section-form__form_content {
            flex-direction: column;
            width: 48.5%;
            position: relative;
            &-area {
                width: 100%;
                position: static;
                z-index: 0;
                &:before {
                    z-index: -1;
                }
                textarea {
                    height: 221px;
                    background: none;
                    z-index: 2;
                    padding-left: 20px;
                }
                &-btn {
                    position: absolute;
                    bottom: 0;
                    right: 0;
                }
            }
            &-input {
                width: 100%;
                margin-top: 50px;
                input {
                    width: 245px;
                }
                div {
                    margin-bottom: 43px;
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
        }
        .section-form__form_contact {
            display: flex;
            width: 39.5%;
            ul {
                li {
                    img {
                        top: 4px;
                    }
                    &:first-child {
                        margin-bottom: 27px;
                        a {
                            line-height: 30px;
                            
                        }
                    }
                    
                }
                .team-contacts__clock {
                    line-height: 30px;
                }
                &:first-child {
                    margin-right: 38px;
                }
                
            }
            
        }

    }

    &.openImg {
        display: flex;
        .chooseCategory-popup__block {
            display: flex;
        }
    }
    &.contact {
        display: flex;
        position: fixed;
        height: 100%;
        .section-form__form-popup {
            display: block;
        }
        .container {
            display: block;
        }
        .chooseCategory-popup__block_close {

        }
    }


    @media screen and (orientation: portrait) {
        &__block {
            min-height: 50%;
            width: 95%;
            img {
                
            }
        }
        .chooseCategory-popup__block_close {
            top: -52px;
            right: -11px;
        }
        
    }
    @media screen and (max-width: 1199px) {
        .section-form__form-popup {
           padding-left: 50px;
           padding-right: 50px;
           padding-bottom: 50px;
           .form-close {
            top: -50px;
            right: -8px;
            }
        } 
    }
    @media screen and (max-width: 991px) {
        .section-form__form-popup {
           .h2 {
               h2,div {
                   font-size: 77px;
                   line-height: 100px;
               }
               div {
                   height: 49%;
               }
               margin-bottom: 0;
           }
           .section-form__form_content-input div {
                width: 100%;
                input {
                    width: 100%;
                }
            }
           
            
            &_desc {
                margin-top: 0;
            }
            .section-form__form-block {
               margin-top: 0;
               position: relative;
            }
            .section-form__form_content {
                position: static;
                &-area {
                    &:before {
                      top: 60px;
                    }
                } 
            }
            .section-form__form_contact {
                flex-direction: column;
            }

        } 
    }
    @media screen and (max-width: 767px) {
        .section-form__form-popup {
            padding-left: 30px;
            padding-right: 30px;
           .h2 {
               h2,div {
                  font-size: 70px;
                  height: 100px;
               }
               div {
                    height: 47%;
               }
               
           }
            .section-form__form-block {
                margin-top: 30px;
            }
            .section-form__form_content {
           
                &-area {
                   
                    &:before {
                        top: 35px;
                    }
                }
            }
            .section-form__form_contact {
                width: 45%;
                ul {
                    margin-top: 18px;
                }
                
            }

        } 
    }
    @media screen and (max-width: 495px) {
        .section-form__form-popup {
            padding-left: 30px;
            padding-right: 30px;
           .h2 {
               h2,div {
                  font-size: 55px;
               }
           }
            .section-form__form-block {
              flex-direction: column;
            }
            .section-form__form_content {
                width: 100%;
            }
            .section-form__form_contact {
                width: 100%;
                flex-direction: row;
                justify-content: space-between;
                margin-top: 30px;
                ul {
                    width: auto;
                }
                
            }

        } 
    }
    @media screen and (max-width: 416px) {
        .section-form__form-popup {
         padding-bottom: 0px;
           .h2 {
               h2,div {
                    font-size: 50px;
                    height: 60px;
               }
               div {
                    height: 45%;
               }
               
           }
           
            &_desc {
                font-size: 20px;
            }
            
            .section-form__form_content {
                
                &-area {
                
                    
                }
               
            }
            .section-form__form_contact {
                flex-direction: column;
                justify-content: center;
                align-items: center;
                margin-top: 20px;
                ul {
                    width: 185px;
                    &:first-child {
                        margin-right: 0;
                        
                    }
                }
                
            }

        } 
    }
    @media screen and (max-width: 413px) {
        .section-form__form-popup .h2 div {
            height: 57px;
        }
    }
    @media screen and (max-width: 380px) {
        .section-form__form-popup {
            padding-top: 15px;
           .h2 {
               h2,div {
                    font-size: 45px;
               }
               div {
                  height: 54%;
               }
           }

            &_desc {
                font-size: 16px;
            }

        } 
    }
    @media screen and (max-width: 350px) {
        .section-form__form-popup {
           .h2 {
               h2,div {
                    font-size: 39px;
               }

           }
        } 
    }
}

