}
.choosenBrand {
    background: $c-lg;
    padding: 73px 0 40px 0;
    margin-bottom: 17px;



    .row {
        margin: 0;   
    }



    &:last-child {
        margin-bottom: 0;
        padding-bottom: 35px;
        padding-top: 40px;
    }
    &__logo {
        max-width: 100%;
        height: auto;
    }



    &__desc {
        padding-right: 45px;
        margin-bottom: 0;
        height: 100%;
        display: flex;
        align-items: center;
        padding-right: 30px;
    }



    &__link {
        height: 100%;
        display: flex;
        align-items: center;
        a {
            font-family: Futura_L;
            font-size: 18px;
            color: $c-r;
            white-space: nowrap;
            div {
                width: auto;
                display: inline;
                position: relative;
                img {
                    position: absolute;
                    top: 4px;
                    left: 4px;
                    &:nth-child(2) {
                        left: 11px;
                        top: 1px;
                        transition: all 0.5s ease-out;
                    }
                }
                
            }
            &:hover {
                div {
                    img {
                        &:nth-child(2) {
                            transform: translate(2px, -2px) scale(1.1);
                            transition: all 0.5s ease-out;
                            
                        }
                    }
                }
            }
        }
        
    }

    &__sliders {
        margin-top: 40px;
        overflow: hidden;
        
        .slick-track {
            
            
            .slick-slide {
                margin-right: 0px;
                margin-left: 0px;
                overflow: visible;
                position: relative;
                
                img {
                    position: relative;
                    max-width: 100%;
                    height: auto;
                    box-shadow: 0 3px 6px 1px rgba(0,0,0, 0.4);
                    transform: scale(0.9);
                }
                
            }
        }

        .slick-prev {
            left: 40px;
            z-index: 99;
            top: 42%;
            opacity: 1;
            &:before {
                content: url(../img/homepage/meie/arrow-prev.svg);
                filter: brightness(0) invert(1);
                z-index: 999;
            }
            &:after {
                content: "";
                display: block;
                position: absolute;
                width: 0px;
                height: 0px;
                top: 36px;
                left: 30px;
                background: rgba(0,0,0,0.16);
                box-shadow: 0px 0px 60px 30px rgba(0,0,0, 0.8); 
                z-index: -1;
            }
        }
        .slick-next {
            right: 64px;
            top: 42%;
            z-index: 99;
            &:after {
                content: "";
                display: block;
                position: absolute;
                width: 0px;
                height: 0px;
                top: 36px;
                right: 20px;
                background: rgba(0,0,0,0.16);
                box-shadow: 0px 0px 60px 30px rgba(0,0,0, 0.8); 
                z-index: -1;
            }
            &:before {
                z-index: 999;
                filter: brightness(0) invert(1);
                opacity: 99;
                content: url(../img/homepage/meie/arrow-next.svg);
            }
        }
        @media screen and (max-width: 1200px) {
            .slick-prev, .slick-next {
                zoom:90%;
            } 
        }
        @media screen and (max-width: 767px) {
            .slick-prev, .slick-next {
                zoom:75%;
                top: 35%;
            } 
        }
        .slick-prev, .slick-next {
            opacity: 0;
        }
        .slick-prev:before, .slick-next:before {
            opacity: 1;
        }
        &:hover .slick-prev {
            opacity: 1;
            transition: 1s;
        }
        &:hover .slick-next {
            opacity: 1;
            transition: 1s;
        }

    }

    &__btn {
        
        text-align: center;
        margin-top: 50px;
        a {
            font-family: Futura_L;
            font-size: 20px;
            line-height: 50px;
            width: 175px;
            background: #fff;
            
            color: $c-r;
            display: block;
            margin: 0 auto;
            transition: all 0.5s ease-out;
            span {
                font-size: 14px;
                position: relative;
                display: inline-block;
                transition: all 0.5s ease-out;
            }
            &:hover {
                transform: translateY(-5px);
                box-shadow: 0 5px 15px 0.5px rgba(0,0,0, 0.1);
                span {
                    transform: scale(1.6);
                    
                }
            }
        }

        
    }
}

