
// ---------------------------------------------------------------------
// ------------------------------  OUTLET PRODUCT ------------------------------
// ---------------------------------------------------------------------
.outlet-productSection {
    max-width: 100vw;
    overflow: hidden;
    .breadcrumbs {
        font-family: Futura_L;
        font-size: 16px;
        line-height: 19px;
        span {
            position: relative;
            zoom: 40%;
            top: -5px;
        }
    }

    &__cart {
        margin-top: 50px;
        .outlet-productSection__cart__discount {
            font-family: Futura_L;
            font-size: 29px;
            line-height: 35px;
            position: absolute;
            right: -29.865px;
            top: -10px;
            background: $c-r;
            color: #fff;
            box-shadow: 0 3px 3px 1px rgba(0,0,0,.1);
            
            width: 89.73px;
            height: 89.73px;

            display: flex;
            justify-content: center;
            align-items: center;

            transform: rotate(45deg);
            div {
                transform: rotate(-45deg);
            }
        }

        &-img {
            
            width: 100%;
            height: auto;
            object-fit: cover;
            object-position: center;
            min-height: 100%;
        }

        &_content {
            padding-left: 65px;
            
            &-h1 {
                font-family: Futura_L;
                font-size: 48px;
                line-height: 57px;
                margin-bottom: 40px;
                line-height: 1.2em;
                
            }
            &-cost {
                display: flex;
                justify-content: space-between;
                margin-bottom: 45px;
                div {
                    font-family: Futura_L;
                    span {
                        font-family: Futura_M;
                        font-size: 28px;
                        line-height: 34px;
                    }
                    &:nth-child(1) {
                        font-size: 22px;
                        line-height: 26px;
                        color: $c-g;
                        
                        span {
                            position: relative;
                            &:after {
                                content: '';
                                display: block;
                                width: 130%;
                                height: 1px;
                                background: #222;
                                position: absolute;
                                top: 20px;
                                right: -11px;
                                transform: rotate(14deg);
                            }
                        }
                    }
                    &:nth-child(2) {
                        font-size: 23px;
                        line-height: 27px;
                        color: $c-r;
                    }
                }
                
            }

            &-text {
                color: $c-g;
                margin-bottom: 25px;
            }

            &-btn {
                background: none;
                outline: none;
                font-family: Futura_L;
                font-size: 19px;
                line-height: 22px;
                border: 1px solid rgba(119,120,123,.34);
                line-height: 50px;
                width: 160px;
                
                transition: 0.3s all ease;
                img {
                    position: relative;
                    top: -5px;
                    transition: 0.3s all ease;
                    filter: grayscale(1) contrast(1);
                }
                &:focus {
                    outline: none;
                }
                &:hover {
                    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px 1px;
                    color: rgb(191, 73, 73);
                    transform: translateY(-4px);
                    
                    img {
                        transform: rotate(-14deg);
                        filter: none;
                    }
                    
                }
            }
        }
    }

    @media screen and (max-width: 1199px) { 
        .outlet-productSection__cart_content {
            &-h1 {
                margin-bottom: 20px;
            }
            &-cost {
                margin-bottom: 30px;
            }
        }
    }
    @media screen and (max-width: 991px) {
        .outlet-productSection__cart_content {
            padding-left: 0;
            margin-top: 50px;
        }
    }
    @media screen and (max-width: 767px) {
        .outlet-productSection__cart .outlet-productSection__cart__discount {
            right: 15px;
            top: 0px;
        }
        .outlet-productSection__cart_content-btn {
            margin: 0 auto;
            display: block;
        }
    }
    
    @media screen and (max-width: 400px) {
        .outlet-productSection__cart .outlet-productSection__cart__discount {
            zoom: 80%;
        }
        .outlet-productSection__cart_content {
            margin-top: 40px;
            &-h1 {
                font-size: 40px;
                line-height: 50px;
            }
            &-cost {
                div {
                    span {
                        font-size: 24px;
                    }
                    &:nth-child(1) {
                        font-size: 20px;
                    }
                    &:nth-child(2) {
                        font-size: 21px;
                    }
                }
                
            }
        }
        
    }

    @media screen and (max-width: 360px) {
        .outlet-productSection__cart_content {
            &-h1 {
                font-size: 36px;
            }
        }
        
    }
    
}







// ---------------------------------------------------------------------
// ------------------------------  OTHER PRODUCT ------------------------------
// ---------------------------------------------------------------------


.otherProduct .choosenBrand__sliders .slick-slide  {
    cursor: pointer;
}
.otherProduct {
    padding: 0;
    &-popup {
        background: rgba(0,0,0, 0.7);
        position: fixed;
        width: 100vw;
        height: 100vh;
        top: 0;
        z-index: 9999;
        
        justify-content: center;
        align-items: center;
        display: none;
        
        &__block {
            width: auto;
            height: auto;
            width: 75%;
            max-height: 90%;
            display: flex;
            position: relative;
            display: none;
            
            .slick-prev {
                left: -60px;
                z-index: 99;
                top: 42%;
                opacity: 0;
                transform: translateX(200px);
                transition: 0.5s all ease;
                position: absolute;
                z-index: 1;
                &:after {
                    content: "";
                    display: block;
                    position: absolute;
                    width: 0px;
                    height: 0px;
                    top: 36px;
                    left: 30px;
                    background: rgba(0,0,0,0.16);
                    box-shadow: 0px 0px 100px 50px rgba(0,0,0, 1); 
                    z-index: -1;
                }
                &:before {
                    content: url(../img/homepage/meie/arrow-prev.svg);
                    filter: contrast(250%);
                    z-index: 2;
                    z-index: 9999999;
                }
            }
            .slick-next {
                right: -40px;
                top: 42%;
                z-index: 1;
                transform: translateX(-200px);
                transition: 0.5s all ease;
                opacity: 0;
                &:after {
                    content: "";
                    display: block;
                    position: absolute;
                    width: 0px;
                    height: 0px;
                    top: 36px;
                    right: 20px;
                    background: rgba(0,0,0,0.16);
                    box-shadow: 0px 0px 100px 50px rgba(0,0,0, 1); 
                    z-index: -1;
                }
                &:before {
                    z-index: 999;
                    filter: brightness(0) invert(1) contrast(250%);
                    opacity: 1;
                    content: url(../img/homepage/meie/arrow-next.svg);
                }
            }
            &:hover {
                .slick-prev {
                    opacity: 1;
                    transform: translateX(100px);
                }
                .slick-next {
                    opacity: 1;
                    transform: translateX(-100px);
                }
            }
            &_close {
                position: absolute;
                top: -7px;
                right: -60px;
                color: #fff;
                font-size: 30px;
                padding: 3px;
                align-items: 30px;
                cursor: pointer;
                width: 50px;
                height: 50px;
                &:before {
                    content: "";
                    display: block;
                    width: 50px;
                    height: 1.5px;
                    background: #fff;
                    
                    transform: rotate(-45deg) translate(-20px,15px);
                }
                &:after {
                    content: "";
                    display: block;
                    width: 50px;
                    height: 1.5px;
                    background: #fff;
                    
                    transform: rotate(45deg) translate(14px,19px);
                }
            }
            img {
                width: 100%;
                max-height: 100%;
                object-fit: cover;
                
            }
        }
        
        &.openImg {
            display: flex;
            .otherProduct-popup {
                display: flex;
            }
        }

        @media screen and (orientation: portrait) {
            &__block {
                min-height: 50%;
                width: 95%;
                img {
                    
                }
            }
            .chooseCategory-popup__block_close {
                top: -52px;
                right: -11px;
            }
            
        }
        @media screen and (max-width: 1199px) {
            .section-form__form-popup {
               padding-left: 50px;
               padding-right: 50px;
               padding-bottom: 50px;
               .form-close {
                top: -50px;
                right: -8px;
                }
            } 
        }
        @media screen and (max-width: 991px) {
            .section-form__form-popup {
               .h2 {
                   h2,div {
                       font-size: 77px;
                       line-height: 100px;
                   }
                   div {
                       height: 49%;
                   }
                   margin-bottom: 0;
               }
               .section-form__form_content-input div {
                    width: 100%;
                    input {
                        width: 100%;
                    }
                }
               
                
                &_desc {
                    margin-top: 0;
                }
                .section-form__form-block {
                   margin-top: 0;
                   position: relative;
                }
                .section-form__form_content {
                    position: static;
                    &-area {
                        &:before {
                          top: 60px;
                        }
                    } 
                }
                .section-form__form_contact {
                    flex-direction: column;
                }
    
            } 
        }
        @media screen and (max-width: 767px) {
            .section-form__form-popup {
                padding-left: 30px;
                padding-right: 30px;
               .h2 {
                   h2,div {
                      font-size: 70px;
                      height: 100px;
                   }
                   div {
                        height: 47%;
                   }
                   
               }
                .section-form__form-block {
                    margin-top: 30px;
                }
                .section-form__form_content {
               
                    &-area {
                       
                        &:before {
                            top: 35px;
                        }
                    }
                }
                .section-form__form_contact {
                    width: 45%;
                    ul {
                        margin-top: 18px;
                    }
                    
                }
    
            } 
        }
        @media screen and (max-width: 495px) {
            .section-form__form-popup {
                padding-left: 30px;
                padding-right: 30px;
               .h2 {
                   h2,div {
                      font-size: 55px;
                   }
               }
                .section-form__form-block {
                  flex-direction: column;
                }
                .section-form__form_content {
                    width: 100%;
                    
                }
                .section-form__form_contact {
                    width: 100%;
                    flex-direction: row;
                    justify-content: space-between;
                    margin-top: 30px;
                    ul {
                        width: auto;
                    }
                    
                }
    
            } 
        }
        @media screen and (max-width: 416px) {
            .section-form__form-popup {
            padding-bottom: 0;
               .h2 {
                   h2,div {
                        font-size: 50px;
                        height: 60px;
                   }
                   div {
                        height: 45%;
                   }
                   
               }
               
                &_desc {
                    font-size: 20px;
                }
                
                .section-form__form_content {
                    
                    
                   
                }
                .section-form__form_contact {
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    margin-top: 20px;
                    ul {
                        width: 185px;
                        &:first-child {
                            margin-right: 0;
                            
                        }
                    }
                    
                }
    
            } 
        }
        @media screen and (max-width: 413px) {
            .section-form__form-popup .h2 div {
                height: 57px;
            }
        }
        @media screen and (max-width: 380px) {
            .section-form__form-popup {
                padding-top: 15px;
               .h2 {
                   h2,div {
                        font-size: 45px;
                   }
                   div {
                      height: 54%;
                   }
               }

                &_desc {
                    font-size: 16px;
                }

            } 
        }
        @media screen and (max-width: 350px) {
            .section-form__form-popup {
               .h2 {
                   h2,div {
                        font-size: 39px;
                   }

               }
            } 
        }
    }




    &-choosenBrand {
        padding-bottom: 35px;
        .choosenBrand__desc {
            color: $c-g;
        }
    }
    &_h3 {
        font-family: Futura_L;
        font-size: 48px;
        
        margin-bottom: 55px;
        line-height: 1.2em;
    }
    @media screen and (max-width: 767px) {
        &_h3 {
            text-align: center;
        }
    }
    @media screen and (max-width: 380px) {
        &_h3 {
            font-size: 40px;
        }
        
    }
    @media screen and (max-width: 330px) {
        &_h3 {
            font-size: 38px;
        }
        
    }
}




