
// ---------------------------------------------------------------------
// ------------------------------  PAGE TITLE ------------------------------
// ---------------------------------------------------------------------

.choose-brands {
    padding-top: 48px;
    h3 {
        margin-top: 44px;
    }
}
.search-brands {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 50px;
    input[type=text]::-ms-clear {  display: none; width : 0; height: 0; }
    input[type=text]::-ms-reveal {  display: none; width : 0; height: 0; }

    input::-webkit-search-decoration,
    input::-webkit-search-cancel-button,
    input::-webkit-search-results-button,
    input::-webkit-search-results-decoration { display: none; }

    input::-webkit-search-decoration,
    input::-webkit-search-cancel-button,
    input::-webkit-search-results-button,
    input::-webkit-search-results-decoration { display: none; }
    &_input {
        width: 60%;
        line-height: 45px;
        outline: none;
        padding-left: 4px;
        font-family: Futura_L;
        font-size: 18px;
        border: 1px solid $c-dg;
        border-left: none;
    }
    &_btn {
        line-height: 45px;
        width: 66px;
        outline: none;
        background: none;
        font-size: 26px;
        color: $c-g;
        border: 1px solid $c-dg;
        border-right: none;
        &:focus {
            outline: none;
        }
    }
}

.allLogoBrands {
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
    padding-bottom: 50px;
    justify-content: space-between;
    div {
        width: 25%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 25px;
        img {
            max-width: 100%;
            height: auto;
            
            -webkit-filter: grayscale(100%);
            -moz-filter: grayscale(100%);
            -ms-filter: grayscale(100%);
            -o-filter: grayscale(100%);
            filter: grayscale(100%);
            filter: gray; /* IE 6-9 */
            filter: grayscale(1);
            transition: all 0.3s ease-out;
            &:hover {
                filter: none;
            }
        }
    }
}
@media screen and (max-width: 991px){
    .allLogoBrands {
        div {
            width: 33.33%;
        }
    }
}
@media screen and (max-width: 767px){
    .allLogoBrands {
        div {
            img {
                zoom: 90%;
            }
        }
    }
}
@media screen and (max-width: 576px){
    .allLogoBrands {
        margin-top: 0;
        div {
            width: 50%;
            img {
                zoom: 100%;
            }
        }
    }
}
@media screen and (max-width: 400px){
    .allLogoBrands {
        div {
            margin-bottom: 15px;
            img {
                zoom: 80%;

            }
        }
    }
}