.pageTitle-outlet {
    background: url(../img/outlet/outlet-bg.jpg) no-repeat center center/ cover;
    
}
.section-outlet {
    padding-top: 45px;
    overflow: hidden;
    &_desc {
        font-size: 18px;
        font-family: Raleway_R;
        line-height: 21px;
        color: $c-g;
        text-align: center;
    }
    .outlet-products {
        margin-top: 30px;
        li {
            margin-top: 60px;
            
        }
    }
    @media screen and (max-width: 576px){
        .outlet-products {
            margin-top: 10px;
            li {
                
            }
        }
    }
}



.top-menu-hp_cartShopping {
    position: relative;
    z-index: 99;
    &:after {
        content: "2";
        display: flex;
        position: absolute;
        width: 20px;
        height: 20px;
        background: #FFB4B4;
        border-radius: 50%;
        justify-content: center;
        align-items: center;
        top: -14px;
        right: -18px;
    }
    .drawer-list__phoneP-drop {
        display: none;
        border-left: none;
        box-shadow: 0px 3px 6px 1px (0,0,0, 0.16);
        padding: 20px 23px 33px 24px;
        @media screen and (min-width: 768px){
            top: 56px;
            right: -123px;
        }
        

        &:before {
            right: 135px;
        }
        .drop-menu {
            padding-left: 0;
            z-index: 99;
            li {
                margin-bottom: 20px;
                a {
                    font-family: Futura_M;
                    font-size: 16px;
                    line-height: 19px;
                }
                font-family: Futura_M;
                font-size: 16px;
                line-height: 19px;
                span {
                    color: $c-r;
                    font-family: Raleway_M;
                    font-size: 14px;
                }
            }
            .drawer-list__phoneP-drop_title {
                a {
                    font-family: Futura_L;
                    font-size: 23px;
                    line-height: 27px;
                    padding-left: 40px;
                    position: relative;
                   display: flex;
                }
                
                img {
                    left: 0;
                }
                &:after {
                    content: "";
                    display: block;
                    position: absolute;
                    width: 100%;
                    height: 1px;
                    background: $c-g;
                    left: 0;
                    bottom: -12px;
                }
            }
            .drawer-list__phoneP-drop_cart {
                margin-top: 5px;
                a {
                    display: flex;
                    justify-content: flex-start;
                    position: relative;
                    
                    img {
                        position: static;
                        width: 81px;
                        height: 55px;
                        margin-right: 13px;
                    }
                    .drawer-list__phoneP-drop_cart-inside {
                        div:nth-child(2) {
                            font-family: Raleway_R;
                            font-size: 14px;
                            color: $c-g;
                            margin-top: 5px;
                        }
                    }
                }
                
                &:after {
                    content: "";
                    display: block;
                    position: absolute;
                    width: 100%;
                    height: 1px;
                    background: $c-g;
                    left: 0;
                    bottom: -15px;
                }
            }
            .drawer-list__phoneP-drop_summa {
                font-family: Raleway_B;
                font-size: 16px;
                line-height: 21px;
                margin-top: 8px;
                span {
                    font-family: Raleway_B;
                    font-size: 16px;
                    position: relative;
                    top: -2px;
                    right: -7px;
                }
            }
            button {
                background: none;
                border: 1px solid #FFB4B4;
                line-height: 50px;
                width: 97%;
                font-family: Futura_L;
                font-size: 19px;
                transition: 0.5s all ease;
                span {
                    color: #FFB4B4;
                    font-size: 23px;
                    font-family: Futura_B;
                    transition: 0.5s all ease;
                }
                &:hover {
                    box-shadow: 0px 6px 12px 1px rgba(0,0,0, 0.25);
                    transform: translateY(-4px);
                    border: 1px solid $c-r;
                    span {
                        color: $c-r;
                    }
                }
            }
        }
    }
    
}
.cartShopping__mob {
    min-width: 55px;
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border-radius: 50%;
    filter: grayscale(1);
    transition: .4s ease-out;
    &:after {
        top: -10px;
        right: -10px;
        
    }
    .basket-img {
        display: block;
        zoom: 120%;
        transform: rotate(-30deg);
        transition: .4s ease-out;
    }
    .drawer-list__phoneP-drop {
        right: -123px;
        top:75px;
    }

    &:hover {
        filter: none;
        transform: translateY(-5px);
        box-shadow: 0 3px 10px 1px rgba(0,0,0,.26);
        .basket-img {
            
            transform: rotate(0deg);
        }
    }
    &.open {
        filter: none;
        transform: translateY(-5px);
        box-shadow: 0 3px 10px 1px rgba(0,0,0,.26);
        
    }
}
@media screen and (min-width: 768px){
    .top-menu-hp_cartShopping.open {
        .drawer-list__phoneP-drop {
            display: block;
        }
    }
    .cartShopping__mob {
        display: none;
    }
    
}
@media screen and (max-width: 767px){
    .top-menu-pages .drawer-list__logo-outlet {
        margin: 0 20px;
        
    }
    .top-menu-hp_cartShopping {
        &:after {
            display: none;
        }
    }
    .cartShopping__mob{
        &:after {
            display: flex;
        }
        &.open {

            .drawer-list__phoneP-drop {
                display: block;
            }
        }
    }
    
}

@media screen and (max-width: 670px){
    .cartShopping__mob .drawer-list__phoneP-drop {
        right: -200px;
        &:before {
            right: 212px;
        }
    }
}
@media screen and (max-width: 500px) {
    .cartShopping__mob {
        zoom: 80%;
    }
}
@media screen and (max-width: 412px) {
    .cartShopping__mob .drawer-list__phoneP-drop {
        right: -250px;
        &:before {
            right: 262px;
        }
    }
    .top-menu-pages .drawer-list__logo-outlet {
        zoom: 70%;
    }
}
@media screen and (max-width: 360px) {
    .cartShopping__mob {
        zoom: 70%;
    }
    .top-menu-pages .drawer-list__phoneP-outlet{
        zoom: 70%;
    }
    .top-menu-pages .drawer-list__logo-outlet {
        zoom: 60%;
    }
}