
// ---------------------------------------------------------------------
// ------------------------------  CONTACT ------------------------------
// ---------------------------------------------------------------------

.section-form {
    background: url(../img/contact/contact-bg.jpg) no-repeat center center / cover;
    padding-bottom: 0;
    
}
.section-form__form {
    background: #fff;
    padding: 45px 70px 21px 70px;
    margin: 0 70px;
    box-shadow: 0 3px 6px 1px rgba(0,0,0, 0.16);
    transform: translateY(300px);
    h2 {
        margin-bottom: 0;
    }
    &_content {
        display: flex;
        margin-top: 43px;
        .section-form__form_content-area {
            position: relative;
            &:before {
                content: "Teie snum";
                display: block;
                width: 100%;
                height: 21px;
                color: $c-g;
                line-height: 21px;
                position: absolute;
                top: 15px;
                left: 20px;
                -webkit-transition: 0.5s all ease;
                transition: 0.5s all ease;
                z-index: -1;
            }
            &:focus, &:focus-within {
                &:before {
                    -webkit-transform: translate(-62px, -40px) scale(0.8);
                            transform: translate(-62px, -40px) scale(0.8);

                }
            }
            &.active {
                &:before {
                    -webkit-transform: translate(-62px, -40px) scale(0.8);
                            transform: translate(-62px, -40px) scale(0.8);
                }
            }
        }
        .section-form__form_content-area textarea {
            background: none;
        }
        &-input {
            width: 50%;
            display: flex;
            flex-direction: column;
            
            div {
                width: auto;
                height: auto;
                position: relative;
                margin-bottom: 50px;
                &:last-child {
                    margin-bottom: 0;
                }
                &:before {
                    content: "";
                    display: block;
                    width: 100%;
                    height: 21px;
                    color: $c-g;
                    line-height: 21px;
                    position: absolute;
                    top: 0px;
                    left: 0;
                    -webkit-transition: 0.5s all ease;
                    transition: 0.5s all ease;
                    z-index: -1;
                }

                &.theme {
                    
                    &:before {
                        content: "Teema";
                    }
                   
                }
                &.FL-name {
                    &:before {
                        content: "Ees- ja perekonnanimi";
                    }
                }
                &.mail {
                    &:before {
                        content: "E-mail";
                    }
                }
                &:focus, &:focus-within {
                    &:before {
                        -webkit-transform: translate(-42px, -20px) scale(0.8);
                                transform: translate(-42px, -20px) scale(0.8);
    
                    }
                }
                &.active {
                    &:before {
                        -webkit-transform: translate(-42px, -20px) scale(0.8);
                                transform: translate(-42px, -20px) scale(0.8);
                    }
                }

            }
            input {
                width: 300px;
                max-width: 100%;
                line-height: 25px;
                outline: none;
                border: none;
                border-bottom: 1px solid $c-g;          
                color: #143E5E;
                background: none;
                &:focus {
                    outline: none;
                }
                &::placeholder {
                    color: $c-g;
                }

            }

        }
        &-area {
            width: 50%;
            outline: none;
            display: flex;
            flex-direction: column;
                &:focus {
                    outline: none;
                }
            textarea {
                width: 100%;
                height: 180px;
                resize: none; /*    */
                outline: none;
                padding-top: 13px;
                padding-left: 9px;
                color: #143E5E;
                
                    &:focus {
                        outline: none;
                    }
            }
            &-btn {
                outline: none;
                width: 93px;
                line-height: 50px;
                background: none;
                margin-top: 77px;
                font-family: Futura_L;
                font-size: 19px;
                border: 1px solid rgba(119,120,123, 0.34);
                transition: 0.5s all ease;
                span {
                    color: $c-r;
                    opacity: 31%;
                    transition: 1s all ease;
                }
                &:hover {
                    transform: translateY(-5px);
                    box-shadow: 0 3px 6px 1px rgba(0,0,0, 0.16);
                    span {
                        opacity: 1;
                    } 
                }
                &:focus {
                    outline: none;
                }
            }
        }
    }
}
@media screen and (max-width: 1199px){
    .section-form__form_content-area-btn {
        margin-top: 40px;
    }
    .section-form__form {
        transform: translateY(200px);
        &_content-input {
            div {
                &:focus, &:focus-within {
                    &:before {
                        -webkit-transform: translate(-32px, -20px) scale(0.8);
                                transform: translate(-32px, -20px) scale(0.8);
    
                    }
                }
                &.active {
                    &:before {
                        -webkit-transform: translate(-32px, -20px) scale(0.8);
                                transform: translate(-32px, -20px) scale(0.8);
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 991px){

    .section-form__form {
        margin-left: 0;
        margin-right: 0;
        padding-left: 50px;
        padding-right: 50px;
        transform: translateY(100px);
        margin-bottom: -100px;
        &_content {
            flex-direction: column;
            align-items: center;
            &-input {
                width: 80%;
                align-items: center;
                div {
                    &:last-child {
                        margin-bottom: 50px;
                       }
                }
                div {
                    &:focus, &:focus-within {
                        &:before {
                            -webkit-transform: translate(-30px, -20px) scale(0.8);
                                    transform: translate(-30px, -20px) scale(0.8);
        
                        }
                    }
                    &.active {
                        &:before {
                            -webkit-transform: translate(-30px, -20px) scale(0.8);
                                    transform: translate(-30px, -20px) scale(0.8);
                        }
                    }
                }
                input {
                    
                    
                }
            }
            &-area {
                width: 80%;
            }
        }
    }
}
@media screen and (max-width: 767px){
    .section-form__form {
        &_content {
            margin-top: 20px;
            .section-form__form_content-area {
                &:focus, &:focus-within {
                    &:before {
                        -webkit-transform: translate(-52px, -40px) scale(0.8);
                                transform: translate(-52px, -40px) scale(0.8);
    
                    }
                }
                &.active {
                    &:before {
                        -webkit-transform: translate(-52px, -40px) scale(0.8);
                                transform: translate(-52px, -40px) scale(0.8);
                    }
                }
            }
            

            &-input {
                div {
                    margin-bottom: 30px;

                }
                
            }
            
        }
    }
    .section-form__form {
        .h2 {
            zoom: 80%;
        }
    }
}
@media screen and (max-width: 480px){
    .section-form__form {
        padding: 20px 15px;
        &_content {
            .section-form__form_content-area {
                &:focus, &:focus-within {
                    &:before {
                        -webkit-transform: translate(-12vw, -40px) scale(0.8);
                                transform: translate(-12vw, -40px) scale(0.8);
    
                    }
                }
                &.active {
                    &:before {
                        -webkit-transform: translate(-12vw, -40px) scale(0.8);
                                transform: translate(-12vw, -40px) scale(0.8);
                    }
                }
            }
            &-input {
                div {
                    width: 90%;
                    input {
                        width: 100%;
                    }
                    
                        &:focus, &:focus-within {
                            &:before {
                                -webkit-transform: translate(-6vw, -20px) scale(0.8);
                                        transform: translate(-6vw, -20px) scale(0.8);
            
                            }
                        }
                        &.active {
                            &:before {
                                -webkit-transform: translate(-6vw, -20px) scale(0.8);
                                        transform: translate(-6vw, -20px) scale(0.8);
                            }
                        }
                    
                }
                
            }
            
        }
    }
}










// ---------------------------------------------------------------------
// ------------------------------  CONTACT-LINK ------------------------------
// ---------------------------------------------------------------------

.contact-link {
    padding-top: 400px;
    &__block {
        display: flex;
        max-width: 970px;
        margin: 0 auto;
        &_content  {
            width: 50%;
            &-title {
                font-family: Futura_L;
                font-size: 28px;
                line-height: 34px;
                
            }
            .drop-menu {
                margin-top: 30px;
                padding-left: 40px;
                li {
                    position: relative;
                    margin-bottom: 20px;
                    &:last-child {
                        margin-bottom: 0;
                    }
                    img {
                        left: -40px;
                        top: 0;
                        position: absolute;
                    }
                }
                .drop-menu__phone {
                    font-size: 16px;
                    font-family: Raleway_B;
                    
                    span {
                        font-family: Raleway_R;
                        font-size: 14px;
                    }
                }
            }
        }
    }


    @media screen and (max-width:1199px) {
        padding-top: 300px;
        &__block {
            max-width: 970px;
        }
    }
    @media screen and (max-width:1199px) and (min-width: 992px) {
        &__block {
            max-width: 800px;
            .contact-link__block_right  {
                padding-left: 110px;
            }
        }
    }
    @media screen and (max-width:991px) and (min-width: 768px) {
        
        &__block {
            .contact-link__block_left {
                width: 60%;
            }
            .contact-link__block_right  {
                width: 40%
            }
        }
    }
    @media screen and (max-width:767px) {
        padding-top: 210px;
        &__block {
            flex-direction: column;
            .contact-link__block_content {
                width: 100%;
                margin-top: 50px;
            }
        }
    }
}



