// ---------------------------------------------------------------------
// ------------------------------  Salon ------------------------------
// ---------------------------------------------------------------------

.salon {
    h3 {
        text-align: center;
    }
    .desc {
        font-size: 22px;
        line-height: 26px;
        color: $c-g;
        text-align: center;
        margin-top: 32px;
    }
    &__content {
        margin-top: 24px;
        img {
            width: 100%;
            height: auto;
            margin-top: 26px;
        }
    }
    @media screen and(max-width: 500px) {
        .desc {
            font-size: 20px;
        }
    }
}




.salon {
    &-popup {
        background: rgba(0,0,0, 0.7);
        position:fixed;
        width: 100vw;
        height: 100vh;
        top: 0;
        z-index: 9999;
        display: none;
         &.openImg {
            display: flex;
        }


        &__slider {
            width: 50vw;
            height: 95vh;
        
            max-height: 95vh;
            
            position: relative;
            display: block;
            top: 2.5vh;
            margin: 0 auto;

            .slick-slide {
                width: 50vw;
                height: 95vh;
                max-height: 95vh;
                object-fit: cover;
            }
            &_close {
                position: absolute;
                top: -7px;
                right: -60px;
                color: #fff;
                font-size: 30px;
                padding: 3px;
                align-items: 30px;
                cursor: pointer;
                width: 50px;
                height: 50px;
                &:before {
                    content: "";
                    display: block;
                    width: 50px;
                    height: 1.5px;
                    background: #fff;
                    
                    transform: rotate(-45deg) translate(-20px,15px);
                }
                &:after {
                    content: "";
                    display: block;
                    width: 50px;
                    height: 1.5px;
                    background: #fff;
                    
                    transform: rotate(45deg) translate(14px,19px);
                }
            }
            .slick-prev {
                left: -60px;
                z-index: 99;
                top: 42%;
                opacity: 0;
                transform: translateX(200px);
                transition: 0.5s all ease;
                position: absolute;
                z-index: 1;
                &:after {
                    content: "";
                    display: block;
                    position: absolute;
                    width: 0px;
                    height: 0px;
                    top: 36px;
                    left: 30px;
                    background: rgba(0,0,0,0.16);
                    box-shadow: 0px 0px 100px 50px rgba(0,0,0, 1); 
                    z-index: -1;
                }
                &:before {
                    content: url(../img/homepage/meie/arrow-prev.svg);
                    filter: contrast(250%);
                    z-index: 2;
                    z-index: 9999999;
                }
            }
            .slick-next {
                right: -40px;
                top: 42%;
                z-index: 1;
                transform: translateX(-200px);
                transition: 0.5s all ease;
                opacity: 0;
                &:after {
                    content: "";
                    display: block;
                    position: absolute;
                    width: 0px;
                    height: 0px;
                    top: 36px;
                    right: 20px;
                    background: rgba(0,0,0,0.16);
                    box-shadow: 0px 0px 100px 50px rgba(0,0,0, 1); 
                    z-index: -1;
                }
                &:before {
                    z-index: 999;
                    filter: brightness(0) invert(1) contrast(250%);
                    opacity: 1;
                    content: url(../img/homepage/meie/arrow-next.svg);
                }
            }
            &:hover {
                .slick-prev {
                    opacity: 1;
                    transform: translateX(100px);
                }
                .slick-next {
                    opacity: 1;
                    transform: translateX(-100px);
                }
            }
            
            
        }

        
        
        @media screen and (orientation: portrait) {
            &__slider {
                width: 90vw;

                height: 95vh;
            
                max-height: 95vh;
                
                position: relative;
                display: flex;
                justify-content: center;
                align-items: center;
    
                .slick-slide {
                    width: 90vw;

                    height: auto;
                    max-height: 95vh;
                    object-fit: cover;
                }
                &_close {
                    top: -18px;
                    right: -8px;
                }
            }

        }

        
    }
}


