.header-p {
    background: #F2F1F1;
}
.top-menu-pages {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 25px 0;
    
    .drawer-list__logo {
        width: auto;
        display: block;
    }
    
    @media screen and (min-width:768px) {
        .drawer-list {
            flex-grow: 1;
            margin: 0 40px 0 50px;
            .drawer-list__logo {
                zoom: 90%;
                margin-right: 30px;
            }
            .top-menu-hp {
                li.active {
                    border-bottom: 1px solid #111;
                    .withArrow {
                        svg {
                            position: absolute;
                            right: -15px;
                        }
                    }
                }
            }
        }  
    }
    .drawer-list__phoneP {
        display: block;
        position: relative;
        z-index: 99;
        @media screen and (max-width: 767px){
            display: none;
        }
        &-img {
            min-width: 55px;
            height: 55px;
            display: flex;
            justify-content: center;
            align-items: center;
            background: #fff;
            border-radius: 50%;
            box-shadow: 0px 1px 6px 1px rgba(0,0,0,0.16);
            transition: all 0.4s ease-out;
            img {
                filter: grayscale(1);
                transition: all 0.4s ease-out;
            }
        }
        
        &:hover .drawer-list__phoneP-img{
            transform: translateY(-5px);
            box-shadow: 0px 3px 10px 1px rgba(0,0,0,0.26);
            
            img {
                filter: none;
            }
        }
        &-drop {
            position: absolute;
            background: #fff;
            top: 70px;
            right: -80px;
            width: 304px;
            padding: 19px 23px 29px 19px;
            box-shadow: 0px 1px 3px 1px rgba(0,0,0,0.16);
            z-index: 99;
            border-left: 4px solid #77787B;
            display: none;
            &:before {
                content: "";
                display: block;
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 0 17px 17px 17px;
                border-color: transparent transparent #fff transparent;
                top: -17px;
                right: 91px;
                position: absolute;
            }
            .drop-menu {
                width: 100%;
                display: flex;
                flex-direction: column;
                padding-left: 40px;
                z-index: 9;
                li {
                    font-family: Arial;
                    font-size: 16px;
                    line-height: 21px;
                    position: relative;
                    font-weight: normal;
                    margin-bottom: 26px;
                    span {
                        font-weight: normal;
                    }
                    &:last-child {
                        margin-bottom: 0;
                    }
                    img {
                        position: absolute;
                        left: -40px;
                        top: 2px;
                    }


                    
                }
                
                .drop-menu__phone {
                    font-family: Arial;
                    font-size: 16px;
                    font-weight: bold;
                    position: relative;
                    margin-bottom: 34px;
                    span {
                        font-size: 14;
                        font-weight: normal;
                        line-height: 25px;
                    }
                    div {
                        font-family: Futura_L;
                        font-size: 23px;
                        line-height: 27px;
                        font-weight: normal;
                        margin-bottom: 15px;
                    }
                    &:after {
                        content: '';
                        display: block;
                        position: absolute;
                        height: 1px;
                        width: 255px;
                        background: #707070;
                        left: -40px;
                        bottom: -15px;
                    }
                }
            }
        }
        &.active {
            .drawer-list__phoneP-img{
                transform: translateY(-5px);
                box-shadow: 0px 3px 10px 1px rgba(0,0,0,0.26);
                
                img {
                    filter: none;
                    transform: rotate(45deg);
                }
            }
            .drawer-list__phoneP-drop {
                display: block;
            }
        }
    }




    @media screen and (max-width: 1280px) and (min-width: 1200px){
        .drawer-list__phoneP-drop {
            right: -40px;
            &:before {
                right: 51px;
            }
        }
    }
    @media screen and (max-width: 1199px) and (min-width: 768px) {
        .drawer-list__logo {
            zoom: 90%;
        }

        .drawer-list {
            flex-grow: 1;
            margin: 0 20px;
            
            .top-menu-hp {
                li {
                    a {
                        font-size: 18px;
                    }

                    .sub-menu {
                        li {
                            a {
                                font-size: 16px;
                            }
                        }
                    }
                }
                
            }
        }  

        .drawer-list__phoneP {
            
            zoom: 90%;
        }
    }
    @media screen and (max-width: 1090px) and (min-width: 992px){
        .drawer-list__phoneP-drop {
            right: -25px;
            &:before {
                right: 36px;
            }
        }
    }
    

    @media screen and (max-width: 880px) and (min-width: 768px){
        .drawer-list__phoneP-drop {
            zoom: 95%;
            top: 75px;
            right: -15px;
            &:before {
                right: 26px;
            }
        }
    }

    @media screen and (max-width: 767px) {
        justify-content: center;
        .drawer-list__logo {
           margin-right: 50px;
        }
        label.hamburger {
            top: 12px;
        }
    }
    @media screen and (max-width: 530px) and (min-width: 500px){
        .drawer-list__phoneP-drop {
            zoom: 95%;
            top: 75px;
            right: -15px;
            &:before {
                right: 26px;
            }
        }
    }
    @media screen and (max-width: 500px) {
        padding: 20px 0;
        .drawer-list__logo {
          img {
              zoom: 90%;
          }
            
        }
        label.hamburger {
            top: 12px
        }
        .drawer-list__phoneP {
           zoom: 80%;
        }
    }
    @media screen and (max-width: 405px){
        justify-content: flex-start;
        .drawer-list__phoneP-drop {
            zoom: 95%;
            top: 75px;
            right: -15px;
            &:before {
                right: 26px;
            }
        }
       
    }
    @media screen and (max-width: 360px) {
        
        .drawer-list__logo {
           
            
        }
        label.hamburger {
          zoom: 85%;
        }
        .drawer-list__phoneP {
          
        }
    }
    @media screen and (max-width: 345px) {
        .drawer-list__logo {
            img {
                zoom: 80%;
            }
        }
        .drawer-list__phoneP {
           zoom: 80%;
        }
    }
}
