    }
    
    .drawer-list__logo {
        
        
        margin-right: 0px;
        transform: translateX(-50px) scale(0.75);
    
       
    }

    .header-p .top-menu-pages .drawer-list {
        margin: 0px;
        
        .top-menu-hp {
            margin-left: -50px;
            width: 450px;
            li.active {
                
                .withArrow {
                    svg {
                        right: -10px;
                    }
                }
            }
            li {
                a {
                   font-size: 16px;
                }

                .sub-menu {
                    li {
                        a {
                          
                        }
                    }
                }
            }
            
        }
    }  

    .drawer-list__phoneP {
        
        transform: translateX(15px) scale(0.75);
    }
}

@media screen and (min-width: 768px) {
    .header-p{
        // position: fixed;
        width: 100vw;
        z-index: 9999;
        top: 0;
        left: 0;
        transition: top 0.6s;
        &.fixed1 {
            top: -65px;
            .top-menu-pages {
                padding: 5px 0;
            }
        }
        &.fixed2 {
            width:100%;
            position: fixed;
            top: 0;
            transition: top 1s;
            .drawer-list__phoneP-img {
                transform: scale(0.8);
                &:hover {
                    transform: scale(1); 
                }
            }
        }
    }
    .menu-homepage {
        transition: all 1s ease;
        .drawer-list__phoneP-img {
            transition: all 0.6s ease;
        }   
    }
    .menu-homepage_fixed {
        position: fixed;
        width: 100vw;
        background: $c-lg;
        z-index: 9999;
        top: 0;
        opacity: 1;
        
    }
    .section-navHP .drawer-list {
        margin-top: 0;
    }
    
    
    // .main-pages {
    //     padding-top: 84px;
    // }
    .menu-homepage {
        
        .top-menu-pages {
            padding: 19px 0;
            
            .drawer-list__logo_HP {
                    display: none;
            }
            
        }
        &_fixed {
            box-shadow: 1px 2px 9px 1px rgba(0,0,0,0.18);
            padding: 0;
            .drawer-list__phoneP-img {
                transform: scale(0.8);
                &:hover {
                    transform: scale(1) !important; 
                }
            }
            
            .top-menu-pages {
                padding: 5px 0;
                .drawer-list__logo_HP {
                        display: block;
                    }
            }
        }
    }
    
}
@media screen and (max-width: 767px){
    .menu-homepage {
