                            }
                            color: #fff;
                            font-size: 16px;
                        }
                        &:nth-child(1) {
                            filter: grayscale(0.6) brightness(1.5);
                        }
                        &:nth-child(2) {
                            filter: grayscale(1) brightness(1.5);
                            a {
                                color: #999;
                            }
                        }
                    }
                    
                }
                
            }
            .drawer-list__logo_HP {
                    display: block;
                    margin-right: 0;
                    text-align: center;
                    img {
                        filter: brightness(2);
                        zoom: 70%;
                    }
                    @media screen and (max-width: 499px) {
                        
                    }
            }
        }
    }
}



