.header-hp {
    background: $c-dg;
    width: 100%;
    height: 750px;
    max-height: 100vh;
    position: relative;
    overflow: hidden;
    z-index: 0;
    .header-hp__content {
        

        .slick-prev {
            left: 100px;
            z-index: 99;
            top: 42%;
            height: 100%;
            
            &:before {
                content: url(../img/homepage/meie/arrow-prev.svg);
                z-index: 1;
                left: -70px;
                position: relative;
                opacity: 1 ;
            }
            &:after {
                content: "";
                display: block;
                position: absolute;
                width: 0px;
                height: 0px;
                top: 50%;
                left: 30px;
                background: rgba(0,0,0,0.16);
                box-shadow: 0px 0px 100px 50px rgba(0,0,0, 0.8); 
                z-index: -1; 
            }
        }
        .slick-next {
            right: 100px;
            top: 42%;
            &:after {
                content: "";
                display: block;
                position: absolute;
                width: 0px;
                height: 0px;
                top: 50%;
                right: 30px;
                background: rgba(0,0,0,0.16);
                box-shadow: 0px 0px 100px 50px rgba(0,0,0, 0.8); 
                z-index: -1;
            }
            &:before {
                z-index: 1;
                content: url(../img/homepage/meie/arrow-next.svg);
                right: -70px;
                position: relative;
                opacity: 1 !important;
            }
        }
        .slick-prev {
            overflow: hidden;
            width: 200px;
            height: 300px;
            opacity: 0;
        }
        .slick-next {
            overflow: hidden;
            width: 200px;
            height: 300px;
            opacity: 0;
           
            
        }

        &:hover .slick-prev {
            opacity: 1;
            transition: 1s;
            left: 0;
        }
        &:hover .slick-next {
            opacity: 1;
            transition: 1s;
            right: 0;
        }

        @media screen and (max-width: 767px) {
            .slick-prev,.slick-next {
                top: 50%;
            }
        }

        .slick-dots {
            bottom: 30px;
            transform: translateX(20px);
            li {
                button {
                    &:after {
                        content: "";
                        display: block;
                        background: #FFB4B4;
                        width: 20px;
                        height: 3px;
                    }
                }
                &.slick-active {
                    button {
                        &:after {
                            content: "";
                            display: block;
                            background: $c-r;
                            width: 20px;
                            height: 10px;
                            transform: translateY(-6px);
                        }
                    }
                }
            }
        }
        &_item{
            outline: none;
            margin: 0 !important;
            .h1-hp {
                margin-top: 115px;
                position: relative;
                letter-spacing: 0px;
                
                &:after {
                    content: url(../img/homepage/header-line.svg);
                    display: block;
                    position: absolute;
                    top: 100%;
                    margin-top: 10px;
                }
            }
            .main-bg-homepage {
                    width: 54.36896046852123vw;
                    height: auto;
                    min-height: 100%;
                    position: absolute;
                    
                    right: 0;
                    top: 0;
                    
                }
            .main-logo {
                img {
                    width: auto;
                    height: auto;
                }
                
            } 
            .slick-slide {
                width: 100vw;
                
            }
            img {
                width: 100%;
                height: 750px;
                object-fit: cover;
                object-position:center;
                max-height: 100vh;
            }
        }

        &_item-1{
            padding-top: 80px;
           
        }
    }
    
